import React from 'react'
import { BentoGrid, BentoGridItem } from './ui/BentoGrid'

const Grid = () => {
    const gridTitles = [{ title: 'Title1', description: "Description", id: 1 }];
    return (
        <section id="about">
            <BentoGrid>
                {gridTitles.map((item) => (
                    <BentoGridItem
                        id={item.id}
                        key={item.id}
                        title={item.title}
                        description={item.description} />
                ))
                }
            </BentoGrid>
        </section>
    )
}

export default Grid